a {
  text-decoration: none;
  color: #E0ECFF;
}
.filter-modal {
  width: 380px;
  height: 592px;
  background: #203B60;
  box-shadow: 0px 4px 50px rgba(0, 0, 0, 0.5);
  position: absolute;
  right: 0;
  top: 0;
  z-index: 99;
}
.filter-modal .l-toggle .react-toggle-thumb {
  background: #2B4787;
}
.filter-modal .l-toggle .react-toggle--checked .react-toggle-thumb {
  background: #5577FD;
}
.filter-modal .close-icon {
  cursor: pointer;
}
.filter-modal .close-filter-modal {
  height: 107px;
  text-align: right;
  padding-right: 24px;
  line-height: 107px;
}
.filter-modal .staked-only {
  width: 380px;
  height: 48px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-left: 24px;
  padding-right: 24px;
  margin-bottom: 24px;
  color: #FFFFFF;
}
.filter-modal .select-pool-type-network {
  border-bottom: 1px solid #203B60;
}
.filter-modal .select-pool-type-network .pool-type {
  width: 380px;
  height: 184px;
}
.filter-modal .select-pool-type-network .type-cols {
  width: 380px;
  height: 48px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-left: 24px;
  padding-right: 24px;
}
.filter-modal .select-pool-type-network .type-cols .type-title {
  font-size: 14px;
  color: #FFFFFF;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.filter-modal .select-pool-type-network .type-cols .type-title img {
  margin-right: 10px;
  width: 22px;
  height: 22px;
}
.filter-modal .select-pool-type-network .network {
  margin-top: 8px;
  margin-bottom: 16px;
}
.filter-modal .select-footer {
  color: #5577FD;
  font-size: 18px;
  height: 41px;
  line-height: 41px;
}
.filter-modal .select-footer .clear-all-filter {
  text-align: center;
  padding-top: 15px;
  cursor: pointer;
}
